import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    loggedIn: false,
    loginToken: localStorage.getItem('xgVwNyQasbJOmbDI') || '',
    loginName: localStorage.getItem('HwSSluBzxuSTeUsB') || '',
    loginDP: localStorage.getItem('dXobxnkoGsuRDSVR') || 'data:image/jpeg;base64,',
    titleBarText: 'Dashboard'
  },
  mutations: {
    logout (state) {
      state.loggedIn = false
      localStorage.removeItem('xgVwNyQasbJOmbDI')
      localStorage.removeItem('HwSSluBzxuSTeUsB')
      localStorage.removeItem('dXobxnkoGsuRDSVR')
      state.loginToken = ''
      state.loginName = ''
      state.loginDP = 'data:image/jpeg;base64,'
    },
    login (state, payload) {
      state.loggedIn = true
      state.loginToken = payload.token
      state.loginName = payload.name.length ? payload.name : 'Unknown Admin'
      state.loginDP = payload.dp ? payload.dp : 'data:image/jpeg;base64,'
      localStorage.setItem('xgVwNyQasbJOmbDI', payload.token)
      localStorage.setItem('HwSSluBzxuSTeUsB', payload.name.length ? payload.name : 'Unknown Admin')
      localStorage.setItem('dXobxnkoGsuRDSVR', payload.dp ? payload.dp : 'data:image/jpeg;base64,')
    }
  },
  actions: {

  }
})
